    @mixin flex {
        display: flex;
        align-items: center;
    }
    
    $textTitleColor: #26221C;
    $textSecondColor:#7B7B7B;
    $font13:13px;
    $font14:14px;
    $fontWeight400:400;
    $fontWeight500:500;
    $fontWeight600:600;
    $fontWeight700:700;
    $fontWeight800:800;
    .strayDog {
        height: 100%;
        overflow-x: hidden;
        scroll-behavior: smooth;
        * {
            box-sizing: border-box;
            font-family: Poppins;
        }
        .section1 {
            position: relative;
            height: 780px;
            padding: 313px 119px 0;
            background: url('./assets/images/bg1.png') no-repeat top right;
            background-size: 60% auto;
            &>nav {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                z-index: 3;
                display: flex;
                padding: 27px 79px;
                justify-content: space-between;
                align-items: center;
                img {
                    width: 198px;
                }
                button {
                    display: none
                }
                .dropMenu {
                    ul {
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        li {
                            display: inherit;
                            margin: .16rem 0;
                            a {
                                display: inline-block;
                                font-size: 14px;
                                font-weight: 400;
                                color: #51422b;
                                cursor: pointer;
                                &:hover {
                                    color: #FFFFFF;
                                }
                            }
                        }
                    }
                }
            }
            &>h3 {
                font-size: 40px;
                font-weight: bold;
                color: $textTitleColor;
            }
            &>article {
                width: 610px;
                font-size: 22px;
                font-weight: 600;
                color: $textSecondColor;
                padding: 35px 0 79px;
            }
            .section1Bottom {
                &>p {
                    color: $textSecondColor;
                    font-size: $font13;
                }
                .btnGroup {
                    @include flex();
                    gap: 23px;
                    padding: 13px 0 20px;
                    a {
                        min-width: 180px;
                        height: 50px;
                        border-radius: 10px;
                        @include flex();
                        justify-content: center;
                        gap: 8px;
                        font-size: 14px;
                        padding: 0 18px;
                        cursor: pointer;
                        text-decoration: none;
                        &:nth-of-type(1) {
                            color: #fff;
                            background: #D37700;
                        }
                        &:nth-of-type(2) {
                            color: #D37700;
                            border: 1px solid #D37700;
                        }
                    }
                }
            }
            .bgContainer {
                position: absolute;
                top: 0;
                right: 0;
                width: 50%;
                height: 100%;
                z-index: 2;
                .dog1 {
                    position: absolute;
                    right: 0;
                    bottom: 20px;
                    width: 242px;
                    height: 421px;
                    background: url('./assets//images/dog_1.png') no-repeat center;
                    background-size: contain;
                }
                .dog2 {
                    position: absolute;
                    right: 413px;
                    bottom: 10px;
                    width: 318px;
                    height: 314px;
                    background: url('./assets//images/dog_2.png') no-repeat center;
                    background-size: contain;
                }
            }
        }
        .section2 {
            padding-top: 267px;
            background: url('./assets/images/bg2.png') no-repeat center 0;
            background-size: 100% auto;
            .introduceList {
                width: 80%;
                margin: 80px auto 0;
                @include flex();
                justify-content: space-between;
                align-items: stretch;
                li {
                    position: relative;
                    width: 28%;
                    max-width: 380px;
                    // min-height: 450px;
                    padding: 38px 42px;
                    background: rgba(211, 119, 0, 0.1);
                    border: 4px solid #D2C2A9;
                    h4 {
                        font-size: 24px;
                        font-weight: $fontWeight600;
                        text-align: center
                    }
                    p {
                        font-size: $font14;
                        font-weight: $fontWeight500;
                        text-align: center;
                        line-height: 1.45;
                        color: $textSecondColor;
                        padding-top: 50px;
                    }
                    &::before {
                        content: "";
                        display: inline-block;
                        width: 50px;
                        height: 44px;
                        position: absolute;
                        left: 19px;
                        top: 14px;
                        background: url('./assets/images/tip_icon.png') no-repeat center/contain;
                    }
                }
            }
        }
        .section3 {
            position: relative;
            padding: 247px 112px 0;
            background: url('./assets/images/bg4.png') no-repeat 0 0;
            background-size: 100% contain;
            .functionCards {
                position: relative;
                margin-top: 66px;
                @include flex();
                justify-content: space-between;
                align-items: stretch;
                .card {
                    max-width: 45%;
                    width: 800px;
                    padding: 40px 23px;
                    background: rgba(211, 119, 0, 0.06);
                    border: 4px solid #D2C2A9;
                    .cardContItem {
                        @include flex();
                        align-items: flex-start;
                        .cardContItemText {
                            padding-left: 40px;
                            h4 {
                                font-size: 24px;
                                font-weight: bold;
                                color: #D37700;
                            }
                            p {
                                font-size: 14px;
                                font-weight: 500;
                                color: $textSecondColor;
                                padding: 12px 0;
                            }
                        }
                    }
                }
                .dog3 {
                    position: absolute;
                    top: 0;
                    left: -221px;
                    width: 221px;
                    height: 264px;
                    background: url('./assets//images/dog_3.png') no-repeat 0 0;
                    background-size: contain;
                }
            }
        }
        .section4 {
            padding: 250px 0 0;
            .valueCards {
                @include flex();
                justify-content: space-between;
                flex-wrap: wrap;
                padding: 83px 150px 0;
                .card {
                    position: relative;
                    width: 541px;
                    max-height: 45%;
                    height: 415px;
                    &:nth-of-type(1),
                    &:nth-of-type(2) {
                        margin-bottom: 100px;
                    }
                    .cont {
                        position: relative;
                        width: 100%;
                        height: 100%;
                        z-index: 3;
                        background: #F7E7CF;
                        border: 4px solid #D2C2A9;
                        padding: 89px 39px;
                        h4 {
                            font-size: 24px;
                            font-weight: bold;
                            color: #26221C;
                        }
                        p {
                            font-size: 14px;
                            font-weight: $fontWeight500;
                            color: $textSecondColor;
                            padding: 16px 0 0;
                            line-height: 1.55;
                        }
                        &::before {
                            content: '';
                            width: 72px;
                            height: 69px;
                            position: absolute;
                            left: 50%;
                            top: -35px;
                            transform: translateX(-50%);
                            background: url('./assets/images/card_tips.png') no-repeat center/contain;
                            z-index: 10;
                        }
                        &::after {
                            content: '';
                            width: 64px;
                            height: 59px;
                            position: absolute;
                            bottom: 50px;
                            right: 10px;
                            background: url('./assets/images/title_img.png') no-repeat center/contain;
                            z-index: 10;
                        }
                    }
                    .roateDiv {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        transform: rotate(5deg);
                        background: rgba($color: #d1a26d, $alpha: .45);
                        border: 4px solid #d0bda8;
                        z-index: 2;
                    }
                }
            }
            .valueBottom {
                height: 176px;
                position: relative;
                img {
                    width: 183px;
                    border: none;
                    position: absolute;
                    top: -20px;
                    right: 0;
                }
            }
        }
        .section5 {
            position: relative;
            padding: 230px 0 0 0;
            background: url('./assets/images/bg3.png') no-repeat 0 0;
            background-size: 100% contain;
            .subModule {
                padding: 0 214px 80px;
                .title {
                    h3 {
                        font-size: 40px;
                        font-weight: bold;
                        color: #FFFFFF;
                    }
                    p {
                        font-size: 24px;
                        font-weight: 600;
                        color: #FFFFFF;
                        padding-top: 31px;
                        line-height: 1.1;
                    }
                }
                .cP {
                    p {
                        font-size: 16px;
                        font-weight: 400;
                        color: #FFFFFF;
                        text-indent: 2em;
                        &:nth-of-type(1) {
                            padding-top: 31px;
                        }
                        &:nth-of-type(2),
                        &:nth-of-type(3) {
                            padding-top: 10px;
                        }
                    }
                }
            }
            .dogLogo {
                position: absolute;
                left: 0;
                top: 20%;
                width: 136px;
                height: 247px;
                background: url('./assets/images/dog_5.png') no-repeat center/contain;
            }
        }
        .section6 {
            padding: 116px 18% 200px;
            .sectionTitle {
                p {
                    font-size: 24px;
                    font-weight: 500;
                    color: #7B7B7B;
                    padding-top: 33px;
                    // text-align: left;
                    span {
                        color: #d1a26d;
                    }
                }
            }
            .percentage {
                margin: 16px 0 0;
                p {
                    color: $textSecondColor;
                    font-size: 18px;
                    padding: 3px 0;
                }
            }
            .distributeParenct {
                .rechart {
                    display: block;
                    width: 860px;
                    height: 488px;
                    background: url('./assets/images/rechart.png') no-repeat center;
                    background-size: 100%;
                    margin: 85px auto 0;
                }
                .whitepaper {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 6rem;
                    height: 1rem;
                    background: #D37700;
                    border-radius: .10rem;
                    margin: .78rem auto 0;
                    padding: 0 .29rem;
                    &>div {
                        span {
                            display: block;
                            color: #FFFFFF;
                            &:nth-of-type(1) {
                                font-size: .3rem;
                                font-weight: 600;
                            }
                            &:nth-of-type(2) {
                                font-size: .24rem;
                                font-weight: 300;
                            }
                        }
                    }
                    &>i {
                        display: inline-block;
                        width: .44rem;
                        height: .44rem;
                        background: url('./assets/images/arrow_right_icon.png') no-repeat center;
                        background-size: 100%;
                    }
                }
            }
        }
        .section7 {
            padding: 0 120px;
            .futureCards {
                @include flex();
                justify-content: center;
                flex-wrap: wrap;
                gap: 30px;
                margin-top: 69px;
                .card {
                    position: relative;
                    width: 500px;
                    height: 500px;
                    padding: 66px 90px;
                    background: url('./assets/images/card_r_bg.png') no-repeat center;
                    background-size: 100%;
                    img {
                        display: block;
                        margin: 0 auto 58px;
                    }
                    h4 {
                        font-size: 24px;
                        font-weight: 600;
                        color: #26221C;
                        text-align: center
                    }
                    p {
                        font-size: 14px;
                        font-weight: 500;
                        color: #7B7B7B;
                        padding: 28px 0;
                        text-align: center;
                    }
                }
            }
        }
        .section8 {
            padding: 200px 16% 100px;
            .roadmap {
                position: relative;
                padding: 80px 30px;
                li {
                    &:nth-of-type(2),
                    &:nth-of-type(4) {
                        text-align: right;
                    }
                    .card {
                        display: inline-block;
                        position: relative;
                        width: 404px;
                        min-height: 300px;
                        background-size: contain;
                        &::before {
                            content: '';
                            width: 71px;
                            height: 63px;
                            position: absolute;
                            left: 50%;
                            top: -15px;
                            transform: translateX(-50%);
                            background: url('./assets/images/card_tips.png') no-repeat center/contain;
                            z-index: 10;
                        }
                        .roateDiv {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 88%;
                            transform: rotate(5deg);
                            background: rgba($color: #d1a26d, $alpha: .45);
                            border: 4px solid #d0bda8;
                            z-index: 2;
                        }
                        .cont {
                            position: relative;
                            width: 100%;
                            height: 100%;
                            padding: 80px 12px 48px 60px;
                            z-index: 3;
                            background: #F7E7CF;
                            border: 4px solid #D2C2A9;
                            text-align: left;
                            h4 {
                                font-size: 24px;
                                font-weight: bold;
                                color: #26221C;
                            }
                            .lip {
                                margin-top: 28px;
                                p {
                                    font-size: 14px;
                                    font-weight: 400;
                                    color: #7B7B7B;
                                }
                            }
                        }
                    }
                }
                .roadmapLine {
                    position: absolute;
                    top: 5%;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 78px;
                    height: 90%;
                    background: url('./assets/images/line.png') no-repeat center/contain;
                    z-index: 4;
                }
            }
        }
        .section9 {
            padding: 48px 0 63px;
            background: #FFF9F1;
            .organization {
                @include flex();
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                gap: 30px;
                padding-top: 58px;
                li {
                    display: inherit;
                    img {
                        height: 80px;
                    }
                }
            }
        }
        .section10 {
            padding: 110px 20% 149px;
            .btnGroup {
                @include flex();
                justify-content: center;
                align-items: center;
                gap: 70px;
                margin-top: 80px;
                a {
                    @include flex();
                    width: 400px;
                    height: 120px;
                    border-radius: 10px;
                    padding: 0 28px;
                    cursor: pointer;
                    i {
                        display: inline-block;
                        width: 40px;
                        height: 40px;
                    }
                    .txt {
                        padding-left: 30px;
                        span {
                            display: block;
                            color: #FFFFFF;
                            &:nth-of-type(1) {
                                font-size: 30px;
                                font-weight: 600;
                            }
                            &:nth-of-type(2) {
                                font-size: 16px;
                                font-weight: 500;
                            }
                        }
                    }
                    &:nth-of-type(1) {
                        background: #D37700;
                        i {
                            background: url('./assets/images/twitter_icon.png') no-repeat center/contain;
                        }
                    }
                    &:nth-of-type(2) {
                        background: #DEC19E;
                        i {
                            background: url('./assets/images/teleg_icon.png') no-repeat center/contain;
                        }
                    }
                }
            }
        }
        .section11 {
            padding: 0 10%;
            footer {
                width: 100%;
                margin-top: 60px;
                p {
                    font-size: 14px;
                    font-weight: 400;
                    color: #7B7B7B;
                    text-align: center;
                }
                .communtiyLink {
                    padding: 31px 0 80px;
                    justify-content: center;
                }
            }
        }
    }
    
    .sectionTitle {
        //  miheight: 59px;
        text-align: center;
        &.r {
            text-align: right;
        }
        &.l {
            text-align: left;
        }
        &.noIcon {
            h3 {
                background: none;
                padding-left: 0;
            }
        }
        h3 {
            display: inline-block;
            padding-left: 79px;
            background: url('./assets//images/title_img.png') no-repeat left center/contain;
            font-size: 38px;
            color: $textTitleColor;
        }
    }
    
    @media screen and (max-width:960px) {
        .sectionTitle {
            text-align: left !important;
            padding: 0 .2rem;
            h3 {
                display: inherit;
                padding-left: .19rem;
                // background: none;
                background-position: 0 -2px;
                font-size: .44rem;
            }
        }
        .communtiyLink {
            justify-content: center;
            a {
                width: .50rem;
                height: .50rem;
            }
        }
        .strayDog {
            .section1 {
                height: auto;
                padding: 4.42rem .43rem 0;
                background: url('./assets/images/bg1_m.png') no-repeat top right;
                background-size: 100% auto;
                &>nav {
                    padding: .36rem .22rem;
                    z-index: 3;
                    transition: all ease-in-out 170ms;
                    &.open {
                        background: #D1A26D;
                        .dropMenu {
                            height: 4.7rem;
                            opacity: 1;
                            border-top: 1px solid rgba(255, 255, 255, 0.2);
                        }
                        button {
                            background: rgba(0, 0, 0, .2) url('./assets/images/menu_close.png') no-repeat center;
                            background-size: 90%;
                        }
                    }
                    &.close {
                        background: transparent;
                        .dropMenu {
                            height: 0;
                            opacity: 0;
                            border-top-color: transparent;
                        }
                        button {
                            background: rgba(0, 0, 0, .2) url('./assets/images/menu_icon.png') no-repeat center;
                            background-size: contain;
                        }
                    }
                    img {
                        width: 1.98rem;
                        height: .47rem;
                    }
                    button {
                        display: inline-block;
                        background: transparent;
                        border: none;
                        width: .6rem;
                        height: .6rem;
                        padding: 0.08rem;
                        border-radius: .1rem;
                        text-indent: -999rem;
                    }
                    .dropMenu {
                        position: absolute;
                        top: 1.32rem;
                        left: 0;
                        width: 100%;
                        height: 4.6rem;
                        overflow: hidden auto;
                        scroll-behavior: smooth;
                        background: #D1A26D;
                        // border-top: 1px solid rgba(255, 255, 255, 0.2);
                        transition: all ease-in-out 180ms;
                        will-change: auto;
                        ul {
                            flex-direction: column;
                            padding: .23rem .36rem;
                            align-items: flex-start;
                            gap: .16rem;
                            li {
                                a {
                                    display: block;
                                    width: 100%;
                                    font-size: .28rem;
                                    font-weight: 400;
                                    color: #ECECEC;
                                }
                            }
                        }
                    }
                }
                &>h3 {
                    font-size: .44rem;
                }
                &>article {
                    width: 80%;
                    font-size: .28rem;
                    font-weight: 600;
                    color: $textSecondColor;
                    padding: .31rem 0 .63rem;
                }
                .section1Bottom {
                    &>p {
                        color: $textSecondColor;
                        font-size: .22rem;
                    }
                    .btnGroup {
                        flex-direction: column;
                        gap: .30rem;
                        padding: .19rem 0 .39rem;
                        a {
                            width: 100%;
                            font-size: .30rem;
                        }
                    }
                }
                .bgContainer {
                    height: 4.42rem;
                    .dog1 {
                        width: 1.26rem;
                        height: 1.58rem;
                        top: 2.07rem;
                        right: -0.2rem;
                    }
                    .dog2 {
                        width: 1.71rem;
                        height: 1.69rem;
                        top: 2.71rem;
                        right: 2.03rem;
                    }
                }
            }
            .section2 {
                padding-top: 1.35rem;
                background: url('./assets/images/bg2_m.png') no-repeat center 0;
                background-size: 100% auto;
                .introduceList {
                    width: 100%;
                    white-space: nowrap;
                    overflow-x: auto;
                    overflow-y: hidden;
                    margin: .52rem 0 0;
                    li {
                        flex: 0 0;
                        display: inline-block;
                        // width: 4.40rem;
                        min-width: 4.40rem;
                        max-width: 4.4rem;
                        // height: 6rem;
                        // max-height: 6rem;
                        padding: .48rem .30rem;
                        margin-left: .24rem;
                        border-width: .04rem;
                        p {
                            white-space: normal;
                            font-size: .22rem;
                            padding: .34rem 0;
                        }
                        &::before {
                            width: .6rem;
                            height: .54rem;
                            left: .16px;
                            top: .19rem;
                        }
                    }
                }
            }
            .section3 {
                padding: 1.1rem .22rem 0;
                .functionCards {
                    flex-direction: column;
                    margin-top: .52rem;
                    gap: .4rem;
                    .card {
                        width: 100%;
                        max-width: unset;
                        padding: .32rem .3rem;
                        border-width: 0.04rem;
                        .cardContItem {
                            .cardContItemText {
                                padding-left: .19rem;
                                h4 {
                                    font-size: .3rem;
                                }
                                p {
                                    padding-top: .17rem;
                                    font-size: .22rem;
                                }
                            }
                        }
                    }
                }
                .dog3 {
                    display: none;
                }
            }
            .section4 {
                padding-top: 1.1rem;
                .valueCards {
                    padding: .81rem 0;
                    width: 100%;
                    flex-direction: column;
                    gap: .84rem;
                    .card {
                        width: 6.4rem;
                        max-width: unset;
                        min-height: 4.73rem;
                        max-height: 6rem;
                        // padding: .73rem .38rem .2rem;
                        &:nth-of-type(1),
                        &:nth-of-type(2) {
                            margin-bottom: 0;
                        }
                        .cont {
                            padding: .71rem .39rem 0;
                            border-width: .04rem;
                            h4 {
                                font-size: .3rem;
                            }
                            p {
                                font-size: .22rem;
                                padding-top: .26rem;
                            }
                            &::before {
                                width: .72rem;
                                height: .69rem;
                                top: -0.35rem;
                            }
                            &::after {
                                width: .64rem;
                                height: .59rem;
                                bottom: .1rem;
                            }
                        }
                        .roateDiv {
                            border-width: .04rem;
                        }
                    }
                }
                .valueBottom {
                    display: none;
                }
            }
            .section5 {
                margin-top: .81rem;
                padding: 0;
                .subModule {
                    padding: 1.72rem .4rem .71rem;
                    &:nth-of-type(1) {
                        background: url('./assets/images/bg3_m.png') no-repeat 0 0;
                        background-size: 100%;
                    }
                    &:nth-of-type(2) {
                        padding-top: 0;
                        background: url('./assets/images/bg3_m_2.png') no-repeat 0 bottom;
                        background-size: 100%;
                    }
                    .title {
                        h3 {
                            font-size: .44rem;
                        }
                        p {
                            font-size: .28rem;
                            padding-top: .27rem;
                        }
                    }
                    .cP {
                        p {
                            font-size: .22rem;
                            &:nth-of-type(1) {
                                padding-top: .35rem;
                            }
                            &:nth-of-type(2) {
                                padding-top: .20rem;
                            }
                        }
                    }
                }
                .dogLogo {
                    top: .48rem;
                    width: .96rem;
                    height: 1.3rem;
                }
            }
            .section6 {
                padding: .68rem 0 0;
                .sectionTitle {
                    text-align: center !important;
                    display: inline-block;
                    p {
                        font-size: .26rem;
                        padding: .4rem 0 .35rem;
                        text-align: left;
                    }
                }
                .percentage {
                    margin: 0;
                    padding: 0 .2rem;
                    p {
                        font-size: .22rem;
                        padding: .03rem 0;
                    }
                }
                .distributeParenct {
                    .rechart {
